@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap');
* {
    box-sizing: border-box;
}

body {
    font-family: Lato;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0)
}

.container {
    width: 1200px;
    padding: 0 30px;
    margin: 0 auto;
}

@media (max-width: 1200px) {
    .container {
        width: 100%;
    }
}

@media (max-width: 768px) {
    .container {
        width: 100%;
        padding: 0 15px;
    }
}

@media (max-width: 375px) {
    .container {
        width: 375px;
        padding: 0 15px;
    }
}

.developer {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Lato;
    font-size: 100px;
    text-align: center;
    color: #f16d7f;
    flex-grow: 1;
}

