.present {
    background-color: #F1E4E6;
    &__wrap {
        height: 764px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-image: url(../img/present-image.png);
        background-repeat: no-repeat;
        background-position: left -233px bottom;
    }
    &__text {
        font-family: Lato;
        position: relative;
        text-transform: uppercase;
        max-width: 405px;
        height: fit-content;
        margin-left: 437px;
        display: flex;
        &::before {
            display: block;
            content: "";
            width: 12px;
            background-color: #F16D7F;
            margin-right: 20px;
        }
    }
    &__main-header {
        font-weight: 900;
        font-size: 48px;
        line-height: 58px;
        margin: 0;
        color: #222222;
        span {
            font-weight: bold;
            font-size: 32px;
            line-height: 34px;
            margin: 0;
            color: #222222;
            span {
                font-weight: bold;
                font-size: 32px;
                line-height: 38px;
                color: #F16D7F;
            }
        }
    }
}

@media (max-width: 990px) {
    .present {
        &__text {
            margin-left: auto;
        }
        &__main-header {
            font-weight: 900;
            font-size: 44px;
            line-height: 53px;
        }
        &__header {
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
        }
    }
}

@media (max-width: 838px) {
    .present {
        &__wrap {
            height: 367px;
            background-size: 386px;
            background-position: left -108px bottom;
        }
    }
}

@media (max-width: 622px) {
    .present {
        &__wrap {
            height: 363px;
            background-image: none;
        }
        &__text {
            margin: auto;
            &::before {
                margin-right: 15px;
            }
        }
        &__main-header {
            font-weight: 900;
            font-size: 38px;
            line-height: 27px;
            span {
                font-size: 20px;
                line-height: 24px;
                span {
                    font-size: inherit;
                    line-height: inherit;
                }
            }
        }
    }
}

@media (max-width: 375px) {
    .present {
        width: 375px;
    }
}