.item {
    &__header {
        text-transform: uppercase;
    }
    &__description {
        border: 1px solid #EAEAEA;
        padding: 65px 250px;
        z-index: 40;
        position: relative;
        top: -65px;
        z-index: 10;
        background-color: #ffffff;
        text-align: center;
    }
    &__category {
        font-weight: 300;
        font-size: 14px;
        line-height: 17px;
        color: #F16D7F;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 27px;
        &::after {
            display: block;
            content: "";
            width: 63px;
            height: 3px;
            background-color: #EF5B70;
            position: relative;
            bottom: -12px;
        }
    }
    &__name {
        font-weight: 300;
        font-size: 18px;
        line-height: 22px;
        color: #4D4D4D;
        margin-bottom: 49px;
    }
    &__text {
        font-weight: 300;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        color: #5E5E5E;
        margin-bottom: 33px;
    }
    &__cost {
        font-weight: 300;
        font-size: 24px;
        line-height: 29px;
        color: #EF5B70;
        margin-bottom: 65px;
    }
    &__line {
        background-color: #EAEAEA;
    }
    &__sort {
        margin: 0 auto 48px;
        width: fit-content;
    }
    &__add-to-box-btn {
        border: 1px solid #EF5B70;
        color: #EF5B70;
        padding: 15px 57px 15px 73px;
        background-color: transparent;
        position: relative;
        text-align: center;
        transition: all 1s;
        &:hover {
            border-color: #ffffff;
            color: #ffffff;
            background-color: #EF5B70;
            svg {
                fill: #ffffff;
            }
        }
        svg {
            fill: #EF5B70;
            position: relative;
            left: -23px;
            transition: all 1s;
        }
    }
}

.slider {
    height: 777px;
    background-color: #F7F7F7;
    &__image {
        margin: 0 auto;
        width: 597px;
        height: 724px;
    }
    &__link {
        width: 47px;
        &:hover {
            path {
                fill: #F16D7F;
            }
        }
    }
    &__btn {
        width: 47px;
        height: 47px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: rgba(42, 42, 42, 0.15);
    }
}