.feedback {
    display: flex;
    justify-content: space-between;
    padding-top: 97px;
    padding-bottom: 126px;
    position: relative;
    z-index: 10;
    &__wrap {
        // background-image: url(../img/subscribe-bg.jpg);
        // background-position: center bottom;
        // background-size: cover;
        position: relative;
        overflow: hidden;
    }
    &__video {
        width: 100%;
        // height: 100%;
        margin: 0 auto;
        position: absolute;
        bottom: 0;
        z-index: 1;
        filter: grayscale(100%)
    }
}

.reviews {
    max-width: 359px;
    text-align: center;
    &__image {
        width: 96px;
        height: 96px;
        border-radius: 50%;
        background-image: url(../img/avatar.png);
        background-repeat: no-repeat;
        background-position: center;
        margin: 0 auto 30px;
    }
}

.subscribe {
    max-width: 557px;
    &__header {
        text-align: center;
        text-transform: uppercase;
        font-size: 18px;
        line-height: 167.2%;
        /* or 30px */
        text-align: center;
        color: #222224;
        margin-bottom: 30px;
        span {
            display: block;
            font-weight: bold;
            font-size: 24px;
            line-height: 167.2%;
            /* or 40px */
            text-align: center;
            color: #222224;
        }
    }
    &__form {
        width: 356px;
        display: flex;
        margin: 0 auto;
    }
    &__input {
        background: #E1E1E1;
        padding: 7px 64px 6px 22px;
        border: none;
        border-radius: 24px 0 0 24px;
        font-size: 14px;
        line-height: 36px;
        &:active {
            outline: none;
        }
        &:focus {
            outline: none;
        }
        &::placeholder {
            font-size: 14px;
            line-height: 36px;
            color: #222224;
            opacity: 0.67;
            text-transform: capitalize;
        }
    }
    &__button {
        padding: 7px 7px 6px 8px;
        border-radius: 0 24px 24px 0;
        border: none;
        background-color: #F16D7F;
        font-size: 14px;
        line-height: 17px;
        display: flex;
        align-items: center;
        text-align: center;
        color: #FFFFFF;
        transition: background-color 1s;
        &:active {
            outline: none;
        }
        &:focus {
            outline: none;
        }
        &:hover {
            background-color: #E05C6E;
            transition: background-color 1s;
        }
    }
}

@media (max-width: 1140px) {
    .feedback {}
    .reviews {
        // width: 300px;
    }
    .subscribe {}
}

@media (max-width: 768px) {
    .feedback {
        display: block;
        &__video {
            width: 200%;
        }
    }
    .reviews {
        // width: 300px;
        margin: 0 auto;
        margin-bottom: 48px;
    }
    .subscribe {
        margin: 0 auto;
    }
}

@media (max-width: 768px) {
    .feedback {
        &__video {
            width: 300%;
        }
    }
}