.page-head {
    background: #F8F3F4;
    margin-bottom: 0;
    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        padding: 59px 0 60px;
    }
    &__header {
        font-size: 24px;
        line-height: 29px;
        text-transform: uppercase;
        color: #F16D7F;
    }
}