.content {
    &__wrap {
        display: flex;
        justify-content: space-between;
        padding: 64px 0 96px;
    }
}

.registration {
    width: 358px;
    &__block-name {
        font-weight: 300;
        font-size: 16px;
        line-height: 19px;
        color: #222222;
        margin-bottom: 20px;
    }
    &__input {
        width: 100%;
        border: 1px solid #A4A4A4;
        padding: 13px 17px;
        font-weight: 300;
        font-size: 13px;
        line-height: 16px;
        color: #222222;
        margin-bottom: 20px;
        &::placeholder {
            color: #B1B1B1;
        }
    }
    &__block {
        margin-bottom: 10px;
    }
    &__checkbox-wrap {
        display: flex;
        margin-bottom: 33px;
    }
    &__checkbox {
        margin-right: 20px;
        label {
            display: flex;
            align-items: center;
            &::before {
                width: 17px;
                height: 17px;
                display: block;
                content: "";
                border: 1px solid #A4A4A4;
                padding: 2px;
                border-radius: 50%;
                margin-right: 20px;
            }
        }
        input[type="radio"] {
            display: none;
            &:checked~label::before {
                background-color: #f16d7f;
            }
        }
    }
    &__text {
        font-weight: 300;
        font-size: 13px;
        line-height: 16px;
        color: #B1B1B1;
        margin-bottom: 39px
    }
    &__button {
        @include button-style(#ffffff, #F16D7F, none, #e05c6e, #bd0f26, #bd0f26, #ffffff);
        font-size: 14px;
        line-height: 17px;
        svg {
            margin-left: 20px;
        }
    }
}

.loyalty {
    color: #000000;
    max-width: 652px;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    &__header {
        text-transform: uppercase;
        margin-bottom: 22px;
    }
    &__text {
        margin-bottom: 21px;
    }
    &__list {}
    &__list-item {
        list-style: none;
        display: flex;
        align-items: center;
        margin-bottom: 16px;
        &::before {
            display: block;
            content: "";
            width: 20px;
            height: 16px;
            margin-right: 22px;
            background-image: url(../img/check.svg);
        }
    }
}