.filters {
    // height: 134px;
    display: flex;
    padding: 40px 0px 40px 0px;
    position: relative;
    &__wrap {
        position: relative;
        width: 360px;
    }
}

.filter {
    position: absolute;
    padding: 17px;
    z-index: 40;
    top: 0px;
    left: 0;
    &__btn {
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        color: #000000;
        position: relative;
        background-color: transparent;
        border: none;
        text-transform: uppercase;
        cursor: pointer;
        svg {
            margin-left: 11px;
        }
        path {
            fill: #000000;
        }
    }
    &[open] {
        box-shadow: 6px 4px 35px rgba(0, 0, 0, 0.21);
        background-color: #ffffff;
        width: 360px;
        // position: absolute;
        .filter__btn {
            color: #EF5B70;
            margin-bottom: 16px;
            path {
                fill: #EF5B70;
            }
        }
    }
    &__content {
        padding: 17px;
    }
    &__block {
        margin-bottom: 18px;
        &[open] {
            .filter__name {
                color: #EF5B70;
            }
        }
    }
    &__name {
        text-transform: uppercase;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        /* identical to box height */
        color: #6F6E6E;
        border-bottom: 1px solid #EBEBEB;
        cursor: pointer;
        padding: 11px 15px;
        position: relative;
        &::before {
            display: block;
            position: absolute;
            content: "";
            width: 5px;
            height: calc(100% + 1px);
            background-color: #EF5B70;
            left: 0;
            top: 0px;
        }
    }
    &__list {
        font-size: 14px;
        line-height: 17px;
        /* identical to box height */
        color: #6F6E6E;
        padding: 24px 12px;
        min-width: 79px;
    }
    &__item {
        margin-bottom: 11px;
    }
    &__link {
        text-decoration: none;
        color: inherit;
        &:hover {
            text-decoration: underline;
        }
        &:active {
            color: #413f3f;
        }
    }
}

.sort {
    min-width: 360px;
    display: flex;
    position: relative;
    flex-wrap: wrap;
    &__item {
        margin-right: 7px;
        position: relative;
        cursor: pointer;
        padding: 10px 21px 10px 9px;
        &:hover {
            .sort__list {
                display: block;
            }
            svg {
                transform: rotate(180deg);
            }
        }
        &:focus {
            .sort__list {
                display: block;
            }
        }
    }
    &__name {
        display: flex;
        align-items: center;
        text-transform: uppercase;
        svg {
            margin-left: 10px;
        }
    }
    &__list {
        display: none;
        background-color: #FFFFFF;
        box-shadow: 6px 4px 35px rgba(0, 0, 0, 0.21);
        position: absolute;
        padding: 10px;
        z-index: 20;
        top: 31px;
        left: 0;
        width: 100%;
    }
    &__variant {
        margin-bottom: 6px;
    }
    &__marker {
        width: 12px;
        height: 12px;
        border: 1px solid #EBEBEB;
        padding: 2px;
        margin-right: 10px;
    }
    &__input {
        display: none;
        &:checked {
            +.sort__marker {
                background-color: #f16d7f;
            }
        }
    }
    &__label {
        text-transform: uppercase;
        display: flex;
        align-items: center;
    }
}